export function putCN(str){
    for(let i=0;i<wording.length;i++){
        if(str == wording[i][0]){
            return wording[i][1]
        }
    }

    return str
}

export function getAlert(lang){
    if(lang == "zh-CN"){
        return alert.cn
    } else {
        return alert.en
    }
    
}